@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Source+Serif+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, #root {
  background: white;
  width: 100vw !important; }

#menu {
  position: fixed;
  top: 2%;
  right: 0;
  padding: 4%;
  font-size: 1.2em;
  opacity: 1;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease; }

::-webkit-scrollbar {
  display: none; }

#navbar {
  position: fixed;
  width: 20%;
  height: 100%;
  background: #333333;
  border-top: 4px solid #880E4F;
  box-shadow: 2px 0 2px black;
  overflow-y: scroll; }
  @media only screen and (max-width: 768px) {
    #navbar {
      width: 0%;
      background: rgba(0, 0, 0, 0.8); } }
  #navbar header {
    margin-left: 5%;
    font-size: 2em;
    padding-top: 8%;
    color: black; }
    @media only screen and (max-width: 768px) {
      #navbar header {
        font-size: 1.0em;
        background: #880E4F; } }
  #navbar ul {
    margin: 0;
    padding: 0;
    padding-bottom: 9%; }
    #navbar ul a {
      text-decoration: none; }
      #navbar ul a li {
        color: grey;
        border-bottom: 2px solid grey !important;
        box-shadow: 0 1px 1px #1a0000 !important;
        list-style-type: none;
        padding-top: 10%;
        padding-left: 4%;
        box-sizing: border-box;
        font-size: 1.2em; }
        @media only screen and (max-width: 768px) {
          #navbar ul a li {
            padding: 4% !important; } }
        #navbar ul a li:hover {
          color: #880E4F; }

#main-doc {
  margin-left: 20%;
  margin-top: 0;
  width: 80%;
  height: 100%; }
  @media only screen and (max-width: 768px) {
    #main-doc {
      margin-left: 0;
      width: 100%; } }
  #main-doc > header {
    font-size: 2.5em;
    padding: 4%;
    border-top: 4px solid #880E4F;
    border-left: 4px solid black;
    box-shadow: 0px 2px 1px black inset; }
    @media only screen and (max-width: 768px) {
      #main-doc > header {
        font-size: 2.2em; } }
  #main-doc section {
    margin: 4%; }
    @media only screen and (max-width: 768px) {
      #main-doc section {
        margin: 5%; } }
    #main-doc section > header {
      border-bottom: 2px solid #880E4F;
      font-size: 2em;
      font-weight: bold;
      padding-left: 1%; }
      @media only screen and (max-width: 768px) {
        #main-doc section > header {
          font-size: 1.6em; } }
    #main-doc section > article {
      padding: 2%;
      font-size: 1.2em; }
      #main-doc section > article > p {
        font-weight: 400;
        padding: 5px; }
      #main-doc section > article > code {
        display: block;
        padding: 3%;
        color: #e60099;
        background: #e6e6e6;
        border-left: 4px solid #3F51B5; }

#menu {
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media only screen and (max-width: 768px) {
    #menu {
      visibility: visible; } }

.active {
  background: #880E4F;
  color: white; }

.rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease; }

